<template>
  <div id='myHeader' :class="{myHeader:true,noHomepage:navIndex}">
      <nav>
        <ul>
            <li v-for="(item,index) in routerPath" @click="navIndex=index" :class="{active:index==navIndex}"><router-link :to="item.path">{{item.text}}</router-link></li>
            <!-- <li v-for="(item,index) in anchorLink" @click="chose(index,item.link)" :class="{active:index==navIndex}"><a :herf="item.link">{{item.text}}</a></li> -->
        </ul>
      </nav>
  </div>
</template>
<script>
export default {
  name: "myHeader",
  data() {
    return {
      msg: "vue msg",
      routerPath: [
        {
          text: "首页",
          path: "/"
        },
        // {
        //   text: "学习经历",
        //   path: "/studyLife"
        // },
        {
          text: "工作经历",
          path: "/workLife"
        },
        {
          text: "项目经历",
          path: "/projectLife"
        },
        {
          text: "技能",
          path: "/mySkill"
        },
        {
          text: "个人资料",
          path: "/personalData"
        }
      ],
      navIndex: 0
    };
  },
  created: function() {},
  mounted: function() {
    var self = this;
    self.chose(0, "/");
  },
  beforeUpdated: function() {},
  methods: {
    chose: function(index, path) {
      this.navIndex = index;
      this.$router.push(path)
    },
    getCurrentPosition: function() {
      clearTimeout(window.scrollTimer);
      var self = this;
      var clientHeight = document.body.clientHeight;
      var scrollTop = document.documentElement.scrollTop;
      console.log(scrollTop, " ", clientHeight);

        if (scrollTop > clientHeight * 14 / 3) {
          this.navIndex = 5;
        } else if (scrollTop > clientHeight * 11 / 3) {
          this.navIndex = 4;
        } else if (scrollTop > clientHeight * 8 / 3) {
          this.navIndex = 3;
        } else if (scrollTop > clientHeight * 5 / 3) {
          this.navIndex = 2;
        } else if (scrollTop > clientHeight * 2 / 3) {
          this.navIndex = 1;
        } else if (scrollTop >= 0) {
          this.navIndex = 0; 
        }

    }
  }
};
</script>
<style scoped>
.myHeader {
  width: 100%;
  height: 100px;
  background: transparent;
  color: #fff;
  position: fixed;
  top: 0;
  /* background: url("../assets/111.jpg"); */
  background-size: cover;
  z-index: 100;
  transition: all 1s ease 0s;
}
.noHomepage {
  height: 50px;
  background-color: #000;
}
.noHomepage nav {
  bottom: 0;
  right: 0;
  transform: translateX(0);
}
nav {
  height: 50px;
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  transition: all .5s ease 0s;
}
ul {
  width: 100%;
  height: 100%;
}
ul li {
  width: 150px;
  height: 100%;
  float: left;
  /* border: 1px solid #fff; */
  font-size: 20px;
  list-style: none;
  font-weight: 700;
  cursor: pointer;
  line-height: 50px;
}
ul li a {
  display: block;
  color: #fff;
  width: 100%;
  height: 100%;
}
ul li.active a {
  color: red;
}
</style>


