<template>
  <div id="homepage" name="homepage">
    <div class="info">
      <h1>吴&nbsp;&nbsp;&nbsp;&nbsp;梦</h1>
      <h3>英文名称：Maynard</h3>
      <h2>前端开发工程师、网络优化工程师</h2>
      <div class="contactInfo">
        <ul>
          <!-- <li class="weixin" @mouseover="showInfo" @mouseout="hideInfo">
            <span>微信:wumeng911211</span>
          </li>
          <li class="qq" @mouseover="showInfo" @mouseout="hideInfo">
            <span>QQ:415157213</span>
          </li>
          <li class="phone" @mouseover="showInfo" @mouseout="hideInfo">
            <span>手机：17366019929</span>
          </li>
          <li class="email" @mouseover="showInfo" @mouseout="hideInfo">
            <span>邮箱：13770927719@139.com</span>
          </li> -->
          <li v-for="item in contactInfo" :class="item.class" @mouseover="myId = item.id" @mouseout="myId = 0">
            <span v-if="item.id == myId">{{item.info}}</span>
          </li>
        </ul>
      </div>
      <!-- <div class="arrowBox" @click="secondPage">
        <div class="arrow">
        </div>
      </div> -->
    </div>
  </div>
</template>

<script>
export default {
  name: "homepage",
  data() {
    return {
      msg: "homepage",
      contactInfo: [
        { id:1,class: "weixin", info: "微信:wumeng911211" },
        { id:2,class: "qq", info: "QQ:415157213" },
        { id:3,class: "phone", info: "手机：17366019929" },
        { id:4,class: "email", info: "邮箱：13770927719@139.com" }
      ],
      myId:0
    };
  },
  methods: {
    showInfo(e) {
      console.log(e.target.className);
    },
    hideInfo(e) {}
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#homepage {
  width: 100%;
  height: 100%;
  position: relative;
  background: url("../assets/bg-2.jpg") no-repeat;
  background-size: cover;
  color: #fff;
  text-align: center;
}
#homepage .info {
  position: absolute;
  top: 300px;
  left: 50%;
  transform: translateX(-50%);
}
#homepage .info h1 {
  font-size: 60px;
  margin-bottom: 20px;
  font-weight: normal;
}
#homepage .info h2 {
  font-size: 25px;
  font-weight: normal;
}
#homepage .info h3 {
  font-size: 20px;
  margin-bottom: 40px;
  font-weight: normal;
}
#homepage .contactInfo {
  width: 300px;
  height: 64px;
  position: absolute;
  top: 220px;
  left: 50%;
  transform: translateX(-50%);
}
#homepage .contactInfo ul {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
}
#homepage .contactInfo li {
  float: left;
  width: 64px;
  height: 64px;
  background: url("../assets/icon.png") no-repeat;
  border-radius: 20px;
  /* overflow: hidden; */
  cursor: pointer;
  position: relative;
}
#homepage .contactInfo li.weixin {
  background-position: 0 0;
}
#homepage .contactInfo li.qq {
  background-position: -128px 0;
}
#homepage .contactInfo li.phone {
  background-position: -704px 0;
}
#homepage .contactInfo li.email {
  background-position: -1280px 0;
}
#homepage .contactInfo li span {
  position: absolute;
  font-size: 16px;
  bottom: -30px;
  left: 10px;
  height: 20px;
  /* display: none; */
  white-space: nowrap;
}

#homepage .arrowBox {
  position: absolute;
  top: 350px;
  left: 50%;
  transform: translateX(-50%);
}
#homepage .arrow {
  width: 80px;
  height: 80px;
  background: url("../assets/arrow.png") no-repeat;
  background-size: 70% 70%;
  background-position: center center;
  box-sizing: border-box;
  border: 2px solid #fff;
  border-radius: 50%;
  animation: heartbeat 1s linear 0s infinite;
  cursor: pointer;
}
@keyframes heartbeat {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
</style>



