<template>
  <div id='projectLife' name="projectLife">
    <h1>项目经验</h1>
    <div class="projects">
      <ul>
        <li>
          <h2>项目一：</h2>
          <div class="projectInfo">
            <div class="name">
              <b>名称：</b>Find公司官网
            </div>
            <div class="doWhat">
              <b>职责：</b>负责官网首页、产品介绍、产品参数、钢琴教室模块的页面制作，页面图片视频资源加载，后台数据交互的工作，后期官网更新维护的工作
            </div>
            <div class="useWha">
              <b>使用技术：</b>主要使用html、css、less、js、jquery以及部分jquery插件
            </div>
            <div class="address">
              <b>项目地址：</b><a href="http://www.findpiano.cn" target="blank">http://www.findpiano.cn</a>
            </div>
          </div>
          <div class="projectImg" >
            <img src="../assets/proejct-1.png" alt="" @click="viewImg">
          </div>
        </li>
        <li>
          <h2>项目二：</h2>
          <div class="projectInfo">
            <div class="name">
              <b>名称：</b>线下教育管理系统
            </div>
            <div class="doWhat">
              <b>职责：</b>根据产品经理提供的原型图，进行页面制作，配合后端实现机构管理、账号管理、学校、班级、学生、老师、课程的管理包括增删改查功能
            </div>
            <div class="useWha">
              <b>使用技术：</b>主要使用vue、webpack、html、css、js、jquery
            </div>
            <div class="address">
              <b>项目地址：</b><a href="http://www.findpiano.cn/institution" target="blank">http://www.findpiano.cn/institution</a>
            </div>
          </div>
          <div class="projectImg" >
            <img src="../assets/project-2.png" alt="" @click="viewImg">
          </div>
        </li>
        <li>
          <h2>项目三：</h2>
          <div class="projectInfo">
            <div class="name">
              <b>名称：</b>对账管理系统
            </div>
            <div class="doWhat">
              <b>职责：</b>根据产品经理提供的原型图，进行页面的制作，配合后台实现版权方与本公司分成收入情况查看和统计，以及用户购买数据的查看等功能
            </div>
            <div class="useWha">
              <b>使用技术：</b>主要使用vue、webpack、html、css、js、jquery
            </div>
            <div class="address">
              <b>项目地址：</b><a href="http://www.findpiano.cn/bill" target="blank">http://www.findpiano.cn/bill</a>
            </div>
          </div>
          <div class="projectImg" >
            <img src="../assets/project-3.png" alt="" @click="viewImg">
          </div>
        </li>
        <li>
          <h2>项目四：</h2>
          <div class="projectInfo">
            <div class="name">
              <b>名称：</b>钢琴教室
            </div>
            <div class="doWhat">
              <b>职责：</b>使用公司内部混合框架，根据ui及设计完成相应页面的制作，配合原生端完成h5与原生接口对接，通过app的webview显示，使用socketIO实现不同钢琴之间通信，实现混合app开发
            </div>
            <div class="useWha">
              <b>使用技术：</b>主要使用html、css、less、js、jquery、websocket。
            </div>
            <div class="address">
              <b>项目地址：</b><a href="无" target="blank">无</a>
            </div>
          </div>
          <div class="projectImg" >
            <img src="../assets/project-4.png" alt="" @click="viewImg">
          </div>
        </li>
        <li>
          <h2>项目五：</h2>
          <div class="projectInfo">
            <div class="name">
              <b>名称：</b>共享钢琴
            </div>
            <div class="doWhat">
              <b>职责：</b>使用公司内部混合框架，根据ui及设计完成相应页面的制作，配合原生端完成h5与原生接口对接，通过app的webview显示，与微信后台对接，实现微信小程序扫码登录、实现混合app开发
            </div>
            <div class="useWha">
              <b>使用技术：</b>主要使用html、css、less、js、jquery
            </div>
            <div class="address">
              <b>项目地址：</b><a href="无" target="blank">无</a>
            </div>
          </div>
          <div class="projectImg" >
            <img src="../assets/project-5.png" alt="" @click="viewImg">
          </div>
        </li>
        <li>
          <h2>项目六：</h2>
          <div class="projectInfo">
            <div class="name">
              <b>名称：</b>共享钢琴小程序
            </div>
            <div class="doWhat">
              <b>职责：</b>与后台配合完成共享钢琴配套小程序的开发，实现小程序扫码支付使用钢琴的功能，并且可以查看使用时间以及购买时长和练琴券
            </div>
            <div class="useWha">
              <b>使用技术：</b>主要使用wxml、wxss、js和小程序提供的api和方法
            </div>
            <div class="address">
              <b>项目地址：</b><a style="cursor: pointer;" @click="showQRcode">小程序二维码</a>
              <img src="../assets/qrcode.png" alt="" v-if="qrcode">
            </div>
          </div>
          <div class="projectImg" >
            <img src="../assets/project-6.png" alt="" @click="viewImg">
          </div>
        </li>
      </ul>
    </div>
    <transition name="fade">
      <div class="imgView" v-if="show">
        <div class="close" @click="close">X</div>
        <img :src="imgSrc" alt="">
      </div>
    </transition>
  </div>
</template>
<script>
export default {
  name: "projectLife",
  data() {
    return {
      msg: "projectLife",
      imgSrc: "",
      show: false,
      qrcode:false
    };
  },
  methods: {
    viewImg: function(event) {
      this.imgSrc = event.target.src;
      this.show = true;
    },
    close: function() {
      this.show = false;
    },
    showQRcode: function() {
      this.qrcode = !this.qrcode;
    }
  }
};
</script>
<style scoped>
#projectLife {
  width: 100%;
  height: 100%;
  position: relative;
  box-sizing: border-box;
  padding-top: 50px;
  box-sizing: border-box;
  padding: 50px 300px 0;
}
#projectLife h1 {
  font-size: 30px;
  margin-bottom: 20px;
}
#projectLife .projects {
  width: 600px;
  box-sizing: border-box;
  padding-left: 40px;
}
#projectLife .projects li {
  margin-bottom: 10px;
  position: relative;
}
#projectLife .projects li h2 {
  font-size: 20px;
}
#projectLife .projects li .projectInfo {
  padding-left: 40px;
  font-size: 16px;
}
#projectLife .projects li .projectInfo .address a {
  color: blue;
}
#projectLife .projects li .projectInfo .address img {
  position: absolute;
  left: 550px;
  top: -30px;
}
#projectLife .projects li .projectInfo > div {
  margin-bottom: 10px;
}
#projectLife .projects li .projectImg {
  width: 300px;
  height: 100px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: -100%;
  cursor: pointer;
}
#projectLife .projects li .projectImg img {
  max-width: 100%;
  max-height: 100%;
}
#projectLife .imgView {
  width: 1300px;
  height: 800px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.5);
}
#projectLife .imgView img {
  max-width: 90%;
  max-height: 90%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
#projectLife .imgView .close {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  position: absolute;
  top: 10px;
  right: 10px;
  text-align: center;
  line-height: 24px;
  border: 1px solid #fff;
  font-size: 16px;
  cursor: pointer;
  color: #fff;
}
</style>


