<template>
  <div id="app">
    <myHeader></myHeader>
    <router-view/>
    <!-- <homepage></homepage>
    <studyLife></studyLife>
    <workLife></workLife>
    <projectLife></projectLife>
    <myskill></myskill>
    <personalData></personalData> -->
  </div>
</template>

<script>
import header from "./components/header";

import homepage from "./pages/homepage";
import myskill from "./pages/mySkill";
import personalData from "./pages/personalData";
import projectLife from "./pages/projectLife";
import studyLife from "./pages/studyLife";
import workLife from "./pages/workLife";

export default {
  name: "app",
  components: {
    myHeader: header,
    homepage: homepage,
    studyLife: studyLife,
    workLife: workLife,
    projectLife: projectLife,
    myskill: myskill,
    personalData: personalData
  },
  created: function() {
    var clientWidth = document.body.clientWidth;
    var clientHeight = document.body.clientHeight;
    document.getElementById("app").style.height = clientHeight + "px";
    document.getElementById("app").style.width = clientWidth + "px";
  }
};
</script>

<style>
body {
  padding: 0;
  margin: 0;
  font-family: Arial, Helvetica, Arial, Verdana, sans-serif;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  color: #2c3e50;
  width: 100%;
  height: 100%;
}
.pagebody {
  box-sizing: border-box;
  padding-top: 100px;
  background-color: #ccc;
  height: 100%;
  width: 100%;
}
</style>



