import Vue from 'vue'
import Router from 'vue-router'
import homepage from '@/pages/homepage'
import studyLife from '@/pages/studyLife'
import workLife from '@/pages/workLife'
import projectLife from '@/pages/projectLife'
import mySkill from '@/pages/mySkill'
import personalData from '@/pages/personalData'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'homepage',
      component: homepage
    },
    {
      path: '/studyLife',
      name: 'studyLife',
      component: studyLife
    },
    {
      path: '/workLife',
      name: 'workLife',
      component: workLife
    },
    {
      path: '/projectLife',
      name: 'projectLife',
      component: projectLife
    },
    {
      path: '/mySkill',
      name: 'mySkill',
      component: mySkill
    },
    {
      path: '/personalData',
      name: 'personalData',
      component: personalData
    },
  ]
})



